// Sidebar.tsx
// Tokens used: theme.colors.primary, theme.colors.textPrimary, theme.colors.textOnPrimary, theme.colors.background
// Component: Universal AFUED Sidebar with role-based navigation

"use client";

import { useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { LogOut, LayoutDashboard, FileText, BookOpen, ClipboardList, Users } from "lucide-react";
import theme from "@/styles/theme";

interface SidebarProps {
  role: "student" | "lecturer" | "admin" | "parent";
  open: boolean
}

const Sidebar: React.FC<SidebarProps> = ({ role, open }) => {
  const pathname = usePathname();
//   const [open, setOpen] = useState(true);

  const commonLinks = [
    { name: "Dashboard", href: "/dashboard", icon: LayoutDashboard },
  ];

  const roleLinks: Record<string, { name: string; href: string; icon: any }[]> = {
    student: [
      { name: "Course Registration", href: "/dashboard/student/register", icon: BookOpen },
      { name: "Results", href: "/dashboard/student/results", icon: ClipboardList },
      { name: "Transcript Request", href: "/dashboard/student/transcript", icon: FileText },
    ],
    lecturer: [
      { name: "Upload Results", href: "/dashboard/lecturer/upload", icon: ClipboardList },
      { name: "Manage Courses", href: "/dashboard/lecturer/courses", icon: BookOpen },
    ],
    admin: [
      { name: "Approve Results", href: "/dashboard/admin/approve", icon: ClipboardList },
      { name: "Manage Users", href: "/dashboard/admin/users", icon: Users },
      { name: "Generate Transcript", href: "/dashboard/admin/transcripts", icon: FileText },
    ],
    parent: [
      { name: "View Ward Results", href: "/dashboard/parent/results", icon: ClipboardList },
    ],
  };

  const links = [...commonLinks, ...(roleLinks[role] || [])];

  return (
    <aside
      className={`${
        open ? "w-64" : "w-20"
      } bg-white dark:bg-[var(--color-background-dark)] border-r border-gray-200 dark:border-gray-700 flex flex-col p-4 transition-all duration-300 min-h-screen`}
    >
      {/* Logo Section */}
      <div className="flex items-center gap-2 mb-8">
        <div
          className="w-8 h-8 rounded-full"
          style={{ backgroundColor: theme.colors.primary }}
        ></div>
        {open && (
          <h1 className="text-xl font-bold text-gray-800 dark:text-white">AFUED Portal</h1>
        )}
      </div>

      {/* Nav Links */}
      <nav className="flex flex-col gap-2 flex-grow">
        {links.map((link) => {
          const isActive = pathname === link.href;
          const Icon = link.icon;
          return (
            <Link
              key={link.name}
              href={link.href}
              className={`flex  items-center gap-3 px-3 py-2 rounded-lg font-medium transition-colors ${
                isActive
                  ? "bg-primary/200 text-primary font-bold"
                  : "text-textPrimary dark:text-gray-600 hover:bg-primary/20 dark:hover:bg-primary/20 hover:text-primary"
              }`}
            >
              <Icon size={20} />
              {open && <span>{link.name}</span>}
            </Link>
          );
        })}
      </nav>

      {/* Logout Button */}
      <div>
        <button
          className="flex items-center gap-3 px-3 py-2 rounded-lg text-gray-600 dark:text-gray-300 hover:bg-primary/10 dark:hover:bg-primary/20 hover:text-primary w-full"
          onClick={() => {
            localStorage.clear();
            window.location.href = "/";
          }}
        >
          <LogOut size={20} />
          {open && <span>Logout</span>}
        </button>
      </div>
    </aside>
  );
};

export default Sidebar;
